import { useContext, useEffect, useRef, useState } from "react";
import { LoginInfoContext } from "../Index";
import styles from "./Chatting.css"
import * as React from 'react';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { type } from "@testing-library/user-event/dist/type";

export default function Chatting(props){
    //--------------------------()(  )--------------------------------------------//
    //1.   new WebSocket(url)
    //1.      
    // 
    let clientSocket = useRef(null);
    //ref  
    if(!clientSocket.current){
        clientSocket.current = new WebSocket("ws://192.168.17.10:80/chat");
        //onmclose // onerror // onmessage // onopen :    
            //2.  
                //1.  close   
        clientSocket.current.onclose = (e) =>{console.log(e); console.log("")}
                //2.  error   (      )
        clientSocket.current.onerror = (e) =>{console.log(e); console.log("")}
                //3.      
        clientSocket.current.onmessage = (e)=>{
            msgList.push(JSON.parse(e.data))
            setMsgList([...msgList]);
        } 
                //4.  open   
        clientSocket.current.onopen = (e)=>{console.log(e); console.log(" ")}
        console.log(clientSocket);
        
        //2.    
        
        //3.   
    
        //4. 
    }

    //===============================================================================================

    const{logininfo , setLogin} =  useContext(LoginInfoContext)
    
    const onSend = ()=>{
        
        let info = {
            msg : msgInput,                     // 
            fromMname : logininfo.mname ,      //
            date : new Date(),
            type : "msg"
        }
        // 3.     
            // send() :   :
                // Json -> : JSON.stringify(js)
                //  -> json : JSON.parse
        clientSocket.current.send(JSON.stringify(info));
        // 
        setMsgInput("");
    }

    //   
    const onEmoSend = (emo) =>{
        let info ={
            msg : emo,
            fromMname : logininfo.mname,
            date : new Date(),
            type : "emo"
        }
        clientSocket.current.send(JSON.stringify(info));
        handleClose();
    }


    //
    const [msgInput, setMsgInput] = useState("");
    //  
    const [msgList , setMsgList] = useState([]);

    const activeEnter = (e) =>{

        if(e.keyCode == 13 && e.ctrlKey){
            setMsgInput(msgInput + "\n");
            return;
        }

        if(e.keyCode == 13){
            if(msgInput.length != 0){
                onSend();
            }
        }
    }
    //    
    useEffect ( ()=>{
        let chatcont =  document.querySelector(".chatcont")
        console.log(chatcont.scroll)
        console.log(chatcont.scrollTop); //    
        console.log(chatcont.scrollHeight); //  
        //2.
        chatcont.scrollTop = chatcont.scrollHeight; //    
    })

    // : , 
    const checkdays = (date) =>{
        console.log(date)
        const nowday = new Date();
        const checkday = new Date(date)
        let year1 = checkday.getFullYear();
        let month1 = checkday.getMonth();
        let day1 = checkday.getDate();

        let year2 = nowday.getFullYear();
        let month2 = nowday.getMonth();
        let day2 = nowday.getDate();

        if (year1 === year2 && month1 === month2 && day1 === day2) {
            console.log("")
            return (<>{checkday.getHours()} {checkday.getMinutes()}</>)
        }
        else{
            console.log("")
            return (<>{year1}-{month1}-{day1}</>)
        }

    }

    //  mui
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };

    // - msg  html  
    const typehtml = (m) => {
        
        if(m.type == "msg"){
            return <div className="content" style={{whiteSpace:"pre-wrap"}}> {m.msg} </div>
        }
        else if (m.type == "emo"){
            return <img src={"/emo/"+m.msg}/>
        }
    }


    return (<>
        <h3>  </h3>
        <div className="chatbox">
            <div className="chatcont">

                {
                    msgList.map( (m)=>{
                        return (<>
                            {
                                logininfo.mname == m.fromMname ? 
                                    (
                                        <div className="rcont">
                                            <div className="subcont">
                                                <div className="date"> { checkdays(m.date) } </div>
                                                {typehtml(m)}
                                            </div>
                                        </div>
                                    ) :
                                    <div className="lcont">
                                        <img className="pimg"  />
                                        <div className="tocont">
                                            <div className="name">{ m.fromMname } </div>
                                            <div className="subcont">
                                                {typehtml(m)}
                                                <div className="date"> { checkdays(m.date)} </div>
                                            </div>
                                        </div>
                                    </div>
                            }       
                        </>);
                    })
                }
            </div>
            <div className="chatbottom">
                <textarea value={msgInput} onChange= { (e)=>{ setMsgInput( e.target.value) }} onKeyDown={activeEnter}> </textarea>
                <button type="button" onClick={ onSend }>  </button>
                
            </div>
            <div>
                <Button
                    id="demo-positioned-button"
                    aria-controls={open ? 'demo-positioned-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={open ? 'true' : undefined}
                    onClick={handleClick}
                >
                    
                </Button>
                <Menu
                    id="demo-positioned-menu"
                    aria-labelledby="demo-positioned-button"
                    anchorEl={anchorEl}
                    open={open}
                    onClose={handleClose}
                    anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                    }}
                    transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                    }}
                >
                    <div style={{height:200, overflowY:"scroll"}}>
                    {
                        Array(43).fill().map( (v,i) =>{
                            return (<><img src={`/emo/emo${i+1}.gif`} onClick={()=>onEmoSend(`emo${i+1}.gif`)}/>
                                { (i+1) % 5 == 0 && <br/>}
                            </>)
                        })

                    }
                    </div>
                </Menu> 
                </div>
        </div>
    </>)
}